import Vue from 'vue'
import Vuex from 'vuex'
import { getUserName, getLimit } from '@/libs/auth'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: getUserName(),
    userlimit: getLimit(),
  },
  mutations: {
    setUserName(state, name) {
      state.username = name;
    },
    setUserLimit(state, limit) {
      state.userlimit = limit;
    }
  },
  actions: {}
})
