import Cookies from 'js-cookie'
const cookieKey = '__user__manager';

const getCookieParams = () => {
    let cookie = Cookies.get(cookieKey);
    if(!cookie) return ''
    else return JSON.parse(cookie)
};
export const userLogin = () => {
    var cookie = getCookieParams();
    return cookie !== '';
};
export const getToken = () => {
    let cookie = getCookieParams();
    return cookie.token;
};
export const getTokenType = () => {
    let cookie = getCookieParams();
    return cookie.tokentype;
};
export const getUserName = () => {
    let cookie = getCookieParams();
    return cookie.username;
};
export const getLimit = () => {
    let cookie = getCookieParams();
    return cookie.limit;
};
export const setCookie = value => {
    var expires = new Date(Date.now() + 24 * 60 * 60 * 1000);
    Cookies.set(cookieKey, JSON.stringify(value), { expires: expires });
};
export const removeCookie = () => {
    Cookies.remove(cookieKey);
};
