import Vue from 'vue'
import Router from 'vue-router'
import { userLogin, getLimit } from '@/libs/auth'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      component: () => import('./views/ErrorPage'),
    },
    {
      path: '/',
      name: 'login',
      component: () => import('./views/Login'),
      meta: {
        requireAuth: false,
        limit: 0,
      }
    },
    /*
    {
      path: '/download',
      name: 'download',
      component: () => import('./views/Download'),
      meta: {
        requireAuth: false,
        limit: 0,
      }
    },
    */
    {
      path: '/index',
      component: () => import('./views/Index'),
      children: [
        {
          path: '',
          component: () => import('./views/Home'),
          meta: {
            requireAuth: true,
            limit: 100,
            title: '项目',
            icon: 'home'
          }
        },
        {
          path: 'resource',
          component: () => import('./views/Resource'),
          meta: {
            requireAuth: true,
            limit: 100,
            title: '资源',
            icon: 'box'
          }
        },
        {
          path: 'alias',
          component: () => import('./views/Alias'),
          meta: {
            requireAuth: true,
            limit: 100,
            title: '分类',
            icon: 'grade'
          }
        },
        {
          path: 'user',
          component: () => import('./views/User'),
          meta: {
            requireAuth: true,
            limit: 300,
            title: '用户',
            icon: 'person'
          }
        },
      ]
    },
    {
      path: '/proj',
      component: () => import('./views/Proj'),
      meta: {
        requireAuth: true,
        limit: 10,
      }
    },
    {
      path: '/files',
      component: () => import('./views/files'),
      meta: {
        limit: 0,
      }
    },
    {
      path: '/list',
      component: () => import('./views/List'),
      meta: {
        requireAuth: true,
        limit: 10,
      }
    },
    {
      name: 'historyversion',
      path: '/historyversion/:id',
      component: () => import('./views/HistoryVersion'),
      props: true,
      meta: {
        requireAuth: true,
        limit: 10,
      }
    },
    {
      name: 'historyproj',
      path: '/historyproj/:id',
      component: () => import('./views/HistoryProj'),
      props: true,
      meta: {
        requireAuth: true,
        limit: 10,
      }
    },
    {
      path: '/error/:code',
      component: () => import('./views/ErrorPage'),
      props: true,
      meta: {
        requireAuth: false,
        limit: 0,
      }
    },
    {
      path: '/test',
      component: () => import('./views/Test'),
      meta: {
        requireAuth: false,
        limit: 0
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  const limit = getLimit() || 0;
  console.log(limit)
  if(!userLogin() && to.meta && to.meta.requireAuth) {
    next({
      path: '/',
      query: { redirect: to.fullPath }
    })
  }
  else if(to.meta && limit < to.meta.limit) {
    next({
      path: '/error/403'
    })
  }
  else {
    next()
  }
})

export default router;
